.home{
  .logo{
    display: flex;
    justify-content: center;
    width: 250px;
    margin: 0 auto;
  }

  .networkButtons{

    display: flex;
    justify-content: center;
    align-items: stretch;
    margin-bottom: 20px;
    gap: 20px;

    .button{
      width: 150px;
      border: 1px solid #8247E5;
      border-radius: 50px;
      padding: 5px 0;
      font-size: 16px;
      text-transform: capitalize;
      font-weight: bold;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #8247E5;
      cursor: pointer;
      line-height: 20px;
      img{
        width: 25px;
        margin-right: 10px;
      }
      &:hover{
        box-shadow: 0 0 16px white;
      }

      .countdown{
        line-height: 15px;
        font-size: 12px;
          letter-spacing: -1px;
        span{

        }

      }
    }
    .bsc{
      border-color:#E58F0E;
      color: #E58F0E;
    }

  }
  .slogan{
    font-size: 16px;
    line-height: 16px;
    color: #000000;
    font-weight: 600;
    text-align: center;
  }
  .buttonGroup{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    margin-top: 20px;
  }

  .whitepaper{
    //display: block;
    width: 130px;
    text-align: center;
    font-size: 15px;
    font-weight: 600;
    text-transform: capitalize;
    border-radius: 10px;
    border: 2px solid #E58F0E;
    background-color: #C6660D;
    padding: 5px 0;
    box-shadow: 0 3px #FEBF33;
    color: white;
    a{
      color: white;
    }

    &:hover{
      cursor: pointer;
    }
    &:active {
      background-color: #FEBF33;
      box-shadow: 0 3px rgba(155, 114, 104, 0.78);
      transform: translateY(2px);
    }
  }
  .mainContent{
    width: 800px;
    max-width: 100%;
    margin: 20px auto 0 auto;
    display: flex;
    justify-content: center;
    align-items: stretch;
    gap: 20px;
    .box{
      width: calc(50% - 10px);
    }
    .leftBox{
      padding: 20px;
      background: #EFEFEF;
      border-radius: 20px;
    }
    .rightBox{
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
    .dataRow{
      display: flex;
      justify-content: space-between;
      align-content: center;
      margin: 5px 0;
      color: #2D374B;
      font-size: 18px;
      line-height: 35px;
      .name{

      }
      .value{
        font-weight: 600;
        font-size: 22px;
        color: #27A0F0;
      }

    }

    .ant-input-affix-wrapper {
      height: 55px;
      margin: 0;
      position: relative;
      border: 2px solid #2D374B;
      box-shadow: none;
      backdrop-filter: blur(20px);
      box-sizing: border-box;
      border-radius: 40px;
      background: #27A0F0;
      .ant-input-suffix,
      .ant-input-prefix {
        color: #FFFFFF;
        font-size: 16px;
        font-weight: 600;
        cursor: pointer;
        user-select: none;
        span {
          color: #FFFFFF;
        }
      }
      .suffix {
        padding-right: 10px;
      }

      .ant-input {
        border: none;
        color: #FFFFFF;
        background: transparent;
        font-size: 16px;
        font-weight: 600;
        line-height: 55px;
        text-align: center;
        padding-left: 25px;
      }

      .ant-input:hover {
        border: none !important;
      }
      .ant-input:focus {
        border: none !important;
        outline: 0;
        box-shadow: none !important;
      }
    }

    .ant-input-affix-wrapper:not(.ant-input-affix-wrapper-disabled):hover{
      border-color: #2D374B !important;
      border-right-width: 2px;
    }

    .ant-input-affix-wrapper-focused {
      outline: 0;
      border: 2px solid #2D374B !important;
      box-shadow: none !important;
    }

    .buyButton{
      border: 2px solid #2D374B;
      border-radius: 30px;
      color: #2D374B;
      font-size: 16px;
      font-weight: 600;
      line-height: 42px;
      padding: 5px 0;
      text-align: center;
      margin-top: 10px;

      &:hover{
        cursor: pointer;
        border: 2px solid #27A0F0;
        color: #27A0F0;
      }
    }

    .actionWrapper{
      margin-top: 30px;
      border-top: 1px dashed #2D374B;
      padding-top: 10px;

      .actionButtons{
        display: flex;
        justify-content: space-between;
        align-items: center;
        div{
          width: calc(50% - 10px);
          text-align: center;
          border: 2px solid #2D374B;
          color: #27A0F0;
          font-weight: bold;
          font-size: 16px;
          padding: 8px 0;
          border-radius: 30px;
          &:hover{
            cursor: pointer;
            border: 2px solid #27A0F0;
            color: #27A0F0;
          }
        }
      }
    }

    .contractInfo{
      background: #2D374B;
      border-radius: 20px;
      padding: 20px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      img{
        width: 25%;
      }

      .data{
        width: 65%;

        h1{
          color:#FFFFFF;
          font-size: 25px;
          font-weight: bold;
          margin-bottom: 10px;
        }
        .dataRow{
          font-size: 17px;
          color: white;
        }
      }

    }

    .referral{
      background: #EFEFEF;
      border-radius: 20px;
      padding: 20px;
      margin-top: 20px;
      h1{
        color:#000000;
        font-size: 25px;
        font-weight: bold;
        margin-bottom: 10px;
      }
      p{
        color:#000000;
      }
      .refWrapper{
        display: flex;
        justify-content: space-between;
        align-items: center;
        .referralLink{
          border: 2px solid #000000;
          box-sizing: border-box;
          border-radius: 40px;
          padding: 5px 20px;
          color: #000000;
          flex-grow: 2;
          text-align: center;
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
          max-width: 70%;
        }
        .copyButton{
          border: 2px solid #000000;
          box-sizing: border-box;
          border-radius: 40px;
          padding: 5px 20px;
          color: #000000;

          &:hover{
            cursor: pointer;
            border: 2px solid #27A0F0;
            color: #27A0F0;
          }
        }
      }
    }
  }

  @media(max-width: 765px){
    .logo{
        width: 200px;
        margin: 0 auto;
      }
    .slogan{
      line-height: 20px;
    }
    .buttonGroup{
      display: flex;
      //flex-direction: column;
    }
    .mainContent{
      margin: 30px auto 0 auto;
      align-items: center;
      flex-direction: column;
      gap: 10px;
      .box{
        width: calc(100% - 20px);
      }

      .referral{
        margin-top: 10px;
      }
    }
  }
}

.blink_me {
  animation: blinker 2s linear 1;
}

@keyframes blinker {
  50% { color: white; transform: scale(1.1) }
}